<template>
  <div class="addNews">

    <el-form ref="form" :model="form" label-width="80px">
      <el-form-item label="新闻标题">
        <el-input v-model="form.title" style="width:50%;"></el-input>
      </el-form-item>
      <el-form-item label="发布单位">
        <el-input v-model="form.author" style="width:50%;"></el-input>
      </el-form-item>
      <el-form-item label="主体内容">
        <editor @getText="getText" v-model="form.content"></editor>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="postForm" v-if="this.$route.name=='通知添加页'">添加</el-button>
        <el-button type="primary" @click="updateForm" v-if="this.$route.name=='通知编辑页'">保存并添加</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  import editor from '../../components/editor'

  export default {
    name: "add",
    components: {
      editor
    },
    data() {
      return {
        form: {
          title: '',
          author: '',
          contentText: '',
          content: ''
        }
      }
    },
    methods: {
      getMsg() {
        let id = this.$route.query.id;
        let params = {id};
        this.$axios.post('getNotices', params).then(res => {
          console.log(res);
          this.form = res.data[0];
        })
      },
      postForm() {
        let params = this.form;
        console.log(params);
        this.$axios.post('addNotices', params).then(res => {
          if (res.code == 200) {
            this.$message({
              message: '添加成功',
              type: 'success'
            });
          } else {
            this.$message.error('添加失败');
          }
        });
        this.$router.push('/notices')
      },
      updateForm() {
        let id = this.$route.query.id;
        let params = {
          ...this.form,
          id
        };

        this.$axios.post('updateNotices', params).then(res => {

        });
        this.$router.push('/notice')
      },
      getText(val) {
        this.form.contentText = val;
        // console.log(this.form.contentText)
        // console.log(this.form.content)
      }
    },
    watch: {
      $route() {
        this.form = {
          title: '',
          contentText: '',
          content: '',
          author: '',
        }
      },
    },
    mounted() {
      if (this.$route.name == "通知编辑页") {
        this.getMsg();
      }
    }
  }
</script>

<style>

</style>

<style scoped>

</style>



